@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Caption&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100vh;
  background-color: rgb(190, 222, 229);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.App {
  text-align: center;
}
.layered-container {
    width: 100%;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.MainContainer {
    width: 60%;
    height: 70%;
    background-color: white;
    border-radius: 5px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
.left {
    background-color: lightblue;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}
.right {
    background-color: white;
    height: 100%;
    overflow-y: auto;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
.br-0 {
    border-right: 0;
}
.description {
    height: 4rem;
}

.leftPage1 {
    background-image:  url(/resumeBuilder/static/media/page1.0ceb3df3.jpg);
}
.leftPage2 {
    background-image:  url(/resumeBuilder/static/media/page2.37fd700f.jpg);
}
.leftPage3 {
    background-image:  url(/resumeBuilder/static/media/page3.49def248.jpg);
}
.leftPage4 {
    background-image:  url(/resumeBuilder/static/media/page4.84615d01.jpg);
}
.leftPage5 {
    background-image:  url(/resumeBuilder/static/media/page5.c75fc4a6.jpg);
}
.leftPage6 {
    background-image:  url(/resumeBuilder/static/media/page6.c20ed19e.jpg);
}
.leftPage7 {
    background-image:  url(/resumeBuilder/static/media/page7.f5c610ec.jpeg);
}
.b-5  {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    -webkit-filter: brightness(0.8);
            filter: brightness(0.8);
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    position: relative;
}
.back-btn {
    text-align: right;
}
.Logo {
    position: absolute;
    bottom: 0;
    right: 0;
}



/* Bootstrap changes */
.form-control {
    font-size: 0.85rem;
}
.form-control:focus, .input-group:focus {
    box-shadow: 0 0 0 0.1rem lightblue;
}
.input-group-text {
    height: 100%;
    border-left: none;
    background-color: transparent;
    height: 100%;
    border-left: none;
    background-color: transparent;
    /* color: white; */
    background: white;
    border-radius: 0;
    border-top-right-radius: 0.25rem;
    border-bottom-right-radius: 0.25rem;
}
.btn-primary {
    background: gray !important;
    border: none !important;
    font-size: 0.8rem !important;
}
.btn-primary:focus {
    box-shadow: 0 0 0 0.1rem lightblue;
}


@media screen and (max-width: 480px) {
    .col-6 {
        -webkit-flex: 0 0 auto;
                flex: 0 0 auto;
        width: 100%;
    }
    .MainContainer {
        width: 80%;
        height: 60%;
    }
    .right {
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
    }
    .next-btn, .back-btn {
        text-align: center;
    }
}

@media screen and (max-width: 768px) {
    .left {
        display: none;
    }
    .right {
        width: 100%;
    }
}

@media screen and (min-width: 1024px) {
    body {
      /* background-color: yellow; */
    }
    .MainContainer {
        /* width: 75%;
        height: 65%; */
    }
}

@media screen and (min-width: 1440px) {
    .MainContainer {
        width: 65%;
        height: 60%;
    }
}
